<template>
    <div class="container">
        <h1>Tabla de Clientes</h1>
        <table>
            <thead>
                <tr>
                    <th>Cliente Id</th>
                    <th>Nombre</th>
                    <th>Dirección</th>
                    <th>Stock</th>
                    <th>Produto Id</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="clients in clients" :key="clients.id">
                    <td>{{ clients.id }}</td>
                    <td>{{ clients.name }}</td>
                    <td>{{ clients.addres }}</td>
                    <td>{{ clients.amount }}</td>
                    <td>{{ clients.product }}</td>
                </tr>
                <tr>

                </tr>
            </tbody>
            
        </table>

    </div>
            <button class="btn-refresh-tbl" type="button" id="refresh" name="refresh" @click="refresh" >Actualizar tabla</button>

</template>



<style scoped>
.container {
    color: #252525;
    z-index: -1;
    background-color: rgba(255, 225, 225, 0.452);
    position: absolute;
    
    max-width: 800px;
    margin: -60% 40%;
    padding: 20px;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    box-shadow: #25252577 2px 2px 10px;


  
   

}

th, td {
    border: 1px solid #ccc;
    padding: 20px;
    text-align: left;
    box-shadow: #2525252f 2px 2px 10px;
}

th {
    background-color: #33333359;
    
}



.btn-refresh-tbl{
    width: 100px;
    background-color:  rgb(245, 245, 245);
    color: #252525;
    box-shadow: #252525 2px 2px 10px;
    position: absolute;
    left: 84%;
