<script>
import axios from 'axios';




export default {

    data(){

        return {

            clients:[],
        };


    },

    methods:{


        obtainClients(){
            axios.get('https://dmksport-production.up.railway.app/api/clients/listall')

            .then((response)=>{


                this.clients=response.data


            })

            .catch((error)=>{

                console.error('error en obtener los clientes', error);
                alert('error en obtener los clientes ');
                
            }); 

        },


        


        refresh(){
            this.obtainClients(); 
  


    }

    },





    mounted(){

        this.obtainClients();

    }


};
</script>