<template>

  <div class="login">

    <h1 class="title">Bienvenido de nuevo!
    </h1>
    <p>
      {{ description }}
    </p>

    <nav>
      <ul class="login_options">

        <li style="border-bottom: gray 01px solid;">iniciar sesion</li>
        <li>
          <router-link to="/register" >Registrarse</router-link> 
          </li>
      </ul> 
    </nav>

    <div class="btns-login">

      <a href="#" class="myButton" style="background-color:whitesmoke ; color: black; border: .5px gray solid;" >Iniciar sesión con Google </a>
      <a href="#" class="myButton" style="background-color:#5c89b6 ; color: white; border: .5px wheat solid;">Iniciar sesión con Facebook </a>

    </div>
    <p style="margin-bottom: 20px;  "> o inciia con</p>

    <form @submit.prevent="login" class="container">

      <div class="input-group">

        <label for="name"></label>

        <input

          type="text"

          id="name"

          name="name"

          v-model="name"

          required

          placeholder="Nombre de usuario o E-mail "

        />

      </div>

      <div class="input-group">

        <label for="password" ></label>

        <input

          type="password"

          id="password"

          name="password"

          v-model="password"

          required

          placeholder="Password* "

        />

      </div>

      <p v-if="error" class="error">Has introducido mal tu nombre de usuario</p>

      <p style="
      color: black;
      width: 40%;
      margin-bottom: 15px; font-size: 13px; border-bottom: gray 01px solid; margin-left: 30%;"> ¿olvido la contraseña?</p>

      <input type="submit" value="Iniciar Sesión" class="button signin-btn" />

    </form>

  </div>

</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      name: "",
      password: "",
      error: false,
      description:"DMK sport somos una empresa dedicada a el comercio de prendas deportivas de alta calidad ",


    };
  },
  methods: {
    async login() {
      try {
        const response = await axios.post(
          "http://localhost:8080/api/users/login",
          {
            name: this.name,

            password: this.password,
          }
        );

        if (response.status === 200) {
          this.$router.push("/clients");
        }
      } catch (error) {
        this.error = true;

        this.errorMessage = error.response
          ? error.response.data
          : error.message; // Captura el mensaje de error

        console.error("Error en el inicio de sesión:", this.errorMessage);
      }
    },
  },
};
</script>

