<template>

    <div class="container">
        <div class="Clientform" >
    
    <h1>Formulario de clientes</h1>

    <form @submit.prevent="save">

            <div class="form-group">
            <label for="id">id</label>
            <input type="text" id="id" name =" id" required v-model="id">
    
    
        </div>
    
    <div class="form-group">
            <label for="name">Nombre</label>
            <input type="text" id="name" name ="name" required v-model="name">
        </div>
        <div class="form-group">
            <label for="address">Dirección</label>
            <input type="" id="addres" name ="addres" required v-model="addres">
        </div>
        <div class="form-group">
            <label for="amount">Stock</label>
            <input type="text" id="amount" name ="amount" required v-model="amount">
        </div>
        <div class="form-group">
            <label for="product">Producto</label>
            <input type="text" id="product" name ="product" required v-model="product">
        </div>
    
    
    
        <button type="submit" id="save" name="save">Guardar</button>
        <button type="button" id="btndelete" name="btndelete" @click="btndelete">Eliminar </button>
        <button type="button" id="update" name="update" @click="update">actualizar</button>
        <button type="button" id="consult" name="consult" @click="consult">Consultar </button>
    </form>
    
    </div>
    
    
    </div>
    
    </template>



<script>
import axios from 'axios';

export default{
    data(){
        return {   
    id:"", 
    name:"", 
    addres:"",
    amount:null,
    product:null
    };
 


    },


   methods:{
    save(){
        axios
        .post('https://dmksport-production.up.railway.app/api/clients', {

    id:this.id, 
    name: this.name,
    addres:this.addres, 
    amount:this.amount, 
    product:this.product,

})
.then((response) =>{
    console.log("", response.data);
    alert("exito");
    this.id = "";
    this.name = "";
    this.addres = "";
    this.amount = "";
    this.product = "";})
.catch((error)=>{
    console.error("error al registrar cliente ", error);})
    },


    update() {

    axios.put('https://dmksport-production.up.railway.app/api/clients/update/'+this.id, {

    name: this.name,

    addres: this.addres, 

    amount: this.amount, 

    product: this.product,

})

.then((response) => {

    alert("Cliente actualizado");

    console.log("Cliente actualizado: ", response.data);
    this.id = "";
    this.name = "";
    this.addres = "";
    this.amount = "";
    this.product = "";
    
    

   
})

.catch((error) => {

    console.error("error al actualizar cliente ", error);

});

},
btndelete(){
        axios
        .delete('https://dmksport-production.up.railway.app/api/clients/'+this.id)
        

        

    .then(() => {

        console.log("Cliente eliminado:");
        alert("Cliente eliminado");
        this.id = "";
        this.name = "";
        this.addres = "";
        this.amount = "";
        this.product = "";


    })

    .catch((error) => {

        console.error("Error al eliminar cliente:", error);
        alert("Error al eliminar cliente");

       

    });

    },
    
    consult(){
        axios
        .get('https://dmksport-production.up.railway.app/api/clients/'+ this.id)
        .then((response) => {

        this.name = response.data.name; // Asignar los datos a la propiedad
        this.addres = response.data.addres; // Asignar los datos a la propiedad
        this.amount = response.data.amount; // Asignar los datos a la propiedad
        this.product = response.data.product // Asignar los datos a la propiedad


})

.catch((error) => {

console.error("Error al obtener clientes:", error);

alert("Error al cargar la lista de clientes");

});




    }




} 

}

</script>

